import { async } from "regenerator-runtime";
import { API_URL } from "./config.js";
import { getJSON } from "./helper.js";
export const state = {
  recipe: {},
  search: {
    page: 1,
    query: "",
    result: [],
    sres: 10,
  },
};

export const loadRecipe = async function (id) {
  try {
    const data = await getJSON(API_URL + `${id}`);
    state.recipe = data.data.recipe;
    state.recipe = {
      title: state.recipe.title,
      cookingTime: state.recipe.cooking_time,
      id: state.recipe.id,
      image: state.recipe.image_url,
      ingredients: state.recipe.ingredients,
      servings: state.recipe.servings,
      publisher: state.recipe.publisher,
    };
  } catch (err) {
    throw err;
  }
};

export const loadSearch = async function (query) {
  try {
    const data = await getJSON(`${API_URL}?search=${query}`);
    if (!data.status) throw Error("invalid request");
    state.search.result = data.data.recipes.map((rec) => {
      return {
        title: rec.title,
        id: rec.id,
        image: rec.image_url,
        publisher: rec.publisher,
      };
    });
  } catch (err) {
    console.info(err);
  }
};

export const updateServings = function (newServings) {
  state.recipe.ingredients.forEach((ing) => {
    ing.quantity = (ing.quantity * newServings) / state.recipe.servings;
  });
  state.recipe.servings = newServings;
};

export const getSearchPage = function (page = state.search.page) {
  state.search.page = page;
  const start = (page - 1) * state.search.sres;
  const end = page * state.search.sres;
  return state.search.result.slice(start, end);
};
