import { values } from "core-js/es/array";
import icon from "../../img/icons.svg";

export default class View {
  _data;
  render(data) {
    this._data = data;
    if (!this._data || (Array.isArray(this._data) && this._data.length === 0))
      return this.randerErrorMessage();
    const mark = this._generateMarkup();
    this.clear();
    this._parentEl.insertAdjacentHTML("beforeend", mark);
  }

  update(data) {
    this._data = data;
    if (!this._data || (Array.isArray(this._data) && this._data.length === 0))
      return this.randerErrorMessage();
    const Newmark = this._generateMarkup();
    const newDom = document.createRange().createContextualFragment(Newmark);
    const curentMarkup = Array.from(this._parentEl.querySelectorAll("*"));
    const newElement = Array.from(newDom.querySelectorAll("*"));

    newElement.forEach((el, i) => {
      const cur = curentMarkup[i];
      if (!el.isEqualNode(cur) && el.firstChild?.nodeValue.trim() !== "")
        cur.textContent = el.textContent;

      if (!el.isEqualNode(cur))
        Array.from(el.attributes).forEach(attr => 
          cur.setAttribute(attr.name, attr.value));
    });
  }

  clear() {
    this._parentEl.innerHTML = "";
  }
  randerSpining() {
    const markUp = `<div class="spinner">
              <svg>
                <use href="${icon}#icon-loader"></use>
              </svg>
            </div>`;
    this.clear();
    this._parentEl.insertAdjacentHTML("beforeend", markUp);
  }

  randerErrorMessage(message = this._ErroMassage) {
    const markUp = `
              <div class="error">
                <div>
                  <svg>
                    <use href="${icon}#icon-alert-triangle"></use>
                  </svg>
                </div>
                <p>${message}</p>
              </div> 
        `;
    this.clear();
    this._parentEl.insertAdjacentHTML("beforeend", markUp);
  }
}
