import View from "./View";
import icons from "../../img/icons.svg";
class pagination extends View {
  _parentEl = document.querySelector(".pagination");

    addHandler(handle){
        this._parentEl.addEventListener('click',function(e){
            const button = e.target.closest('.btn--inline');
            handle(+button.dataset.goto);
        });
    }

  _generateMarkup() {
    const currentPage = this._data.page;
    const pageNums = Math.ceil(this._data.result.length / this._data.sres);

    /// case curently 1 page , and other page
    if (currentPage === 1 && pageNums > 1)
      return `
            <button data-goto = ${currentPage + 1} class="btn--inline pagination__btn--next">
            <span>Page ${currentPage + 1}</span>
            <svg class="search__icon">
              <use href="${icons}#icon-arrow-right"></use>
            </svg>
            </button>`;
    /// curent last page

    if (currentPage === pageNums && pageNums > 1)
      return `
     <button data-goto = ${currentPage - 1} class="btn--inline pagination__btn--prev">
     <svg class="search__icon">
       <use href="${icons}#icon-arrow-left"></use>
     </svg>
     <span>Page ${currentPage-1}</span>
     </button>
    `;

    ///// other page and other pages

    if (currentPage > 1 && currentPage < pageNums) 
    return `
    <button data-goto = ${currentPage - 1} class="btn--inline pagination__btn--prev">
    <svg class="search__icon">
      <use href="${icons}#icon-arrow-left"></use>
    </svg>
    <span>Page ${currentPage - 1}</span>
  </button>
  <button data-goto = ${currentPage + 1} class="btn--inline pagination__btn--next">
    <span>Page ${currentPage+1}</span>
    <svg class="search__icon">
      <use href="${icons}#icon-arrow-right"></use>
    </svg>
  </button> 
    `;

    return "";
  }
}

export default new pagination();
