// https://forkify-api.herokuapp.com/v2

///////////////////////////////////////
////////////////// get Recipes ///////
const getRecipe = async function () {
  try {
    //getting id for recipe
    const id = window.location.hash.slice(1);
    if (!id) return;
    recipeView.randerSpining();

    resultView.render(model.getSearchPage());


    // geting recipe form api
    await model.loadRecipe(id);
    const recipe = model.state.recipe;
    //// randaring recipe to the web site
    recipeView.render(recipe);

    ///// test

  } catch (err) {
    //console.log(err);
    recipeView.randerErrorMessage();
  }
  
};
/////////////////////////////////////////////////
////////////////Search recipes /////////////////

const ControlRecipe = async function () {
  try {
    ////////// rendaring spinner
    resultView.randerSpining();
    ///////// get search result ////////////
    const getQuery = SearchView.getQuery();
    if (!getQuery) return;
    ///////// creating state .////////////
    await model.loadSearch(getQuery);
    ////// rendering search results;
    resultView.render(model.getSearchPage(1));
    /////////////////showing pagination
    pagination.render(model.state.search);
  } catch (err) {
    console.log(err);
  }
};

const controlPagination = function (pageNum) {
  ////// rendering search results;
  resultView.render(model.getSearchPage(pageNum));
  /////////////////showing pagination
  pagination.render(model.state.search);
};

const controlServings = function (newServings) {
  model.updateServings(newServings);
   recipeView.update(model.state.recipe);
};





const init = function () {
  recipeView.addHandlerEvent(getRecipe);
  recipeView.addSevingHandeler(controlServings);
  SearchView.addHandlerSearch(ControlRecipe);
  pagination.addHandler(controlPagination);
};
init();
